*{margin: 0; padding: 0;}
img{display: block;}
li{list-style: none;}

.header{position: fixed; left: 0; top: 0; width: 100%; height: 60px;; z-index: 999; background-color: #FFF; border-bottom: 1px solid #EEE;}
.header .topLogo{position: absolute; left: 5px; top: 15px;}
.header .topLogo img{height: 30px;}
.header .gnbBtn{position: absolute; right: 10px; top: 10px; border: none; background-color: transparent;}

.gnb{position: fixed; top: 60px; left: 0; width: 100%; height: 0; background-color: #000; z-index: 999; overflow: hidden; transition-duration: 0.5s;}
.gnb a{color: #FFF; display: block; text-align: center; line-height: 40px; text-decoration: none;}

.topBanner{position: relative; margin-top: 60px;}
.topBanner .topBg{width: 100%;}
.topBanner h2{position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%); color: #FFF; text-shadow: 1px 1px 4px rgba(0,0,0,0.5);}
.topBanner .topList li{position: absolute; left: 0; top: 0; width: 100%;}
.topBanner .topList li img{width: 100%;}
.topBanner .prev{position: absolute; left: 20px; top: 50%; transform: translateY(-50%); cursor: pointer;}
.topBanner .next{position: absolute; right: 20px; top: 50%; transform: translateY(-50%); cursor: pointer;}

.new{position: relative; width: 90%; margin: 50px auto; overflow: hidden;}
.new h2{font-size: 20px; text-align: center; margin: 50px 0;}
.new .newWrap{position: relative;}
.new .newWrap ul{position: relative; width: 266.64%; overflow: hidden;}
.new .newWrap ul li{width: 12.5%; float: left;}
.new .newWrap ul li img{width: 90%; display: block; margin: 0 auto;}
.new .newWrap .newPrev{position: absolute; left: 20px; top: 50%; transform: translateY(-50%);}
.new .newWrap .newNext{position: absolute; right: 20px; top: 50%; transform: translateY(-50%);}


.about{position: relative; width: 94%; margin: 50px auto;}
.about h2{font-size: 20px; text-align: center; margin-bottom: 50px;}
.about .img_wrap{position: relative; display: flex; flex-direction: column; justify-content: center; align-items: center;}

.lightBox{position: fixed; left: 0; top: 0; width: 100%; height: 100%; background-color: rgba(0,0,0,0.8);}
.lightInner{position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%); width: 70%; padding: 20px; background-color: #FFF; border-radius: 20px; display: flex;}
.lightBox img{width: 50%;}
.lightBox .lightText{width: 50%; text-align: center; display: flex; flex-direction: column; justify-content: center;}
.lightBox .lightText h2{font-size: 18px;}
.lightBox .close{position: absolute; right: 20px; bottom: 20px; background-color: #000; color: #FFF; width: 60px; height: 26px; cursor: pointer;}

.accordion{position: relative; background-color: #EEE; margin: 100px 0; padding: 50px 0;}
.accordion h2{text-align: center; margin-bottom: 50px;}
.accordion ul{width: 90%; margin: 0 auto;}
.accordion ul li{padding-bottom: 5px;}
.accordion li h3{padding: 10px; background-color: #000; border-radius: 10px; color: #FFF; font-size: 14px;}
.accordion li div{padding: 0 10px; height: 60px; font-size: 14px; overflow: hidden; transition-duration: 0.5s;}

.tabs{position: relative; width: 90%; margin: 100px auto;}
.tabs h2{text-align: center; margin-bottom: 50px;}
.tabs .tabsTitle{display: flex;}
.tabs .tabsTitle li{width: 50%; text-align: center; background-color: #EEE; line-height: 50px; margin-bottom: 20px;}
.tabs .tabsTitle li.select{background-color: #222; color: #FFF;}
.tabs .tabsCon div{display: none;}
.tabs .tabsCon div.select{display: block;}
.tabs .tabsCon img{width: 100%;}

.visited{position: relative; width:90%; margin: 0 auto;}
.visited h2{text-align: center; margin-bottom: 50px;}
.visited .inputBox{width: 100%; display: flex; align-items: center; margin-bottom: 10px;}
.visited .inputBox input{width: calc(100% - 60px); height: 28px;}
.visited .inputBox button{width: 60px; height: 32px; background-color: #000; color: #FFF; border: none;}
.visited li{padding: 5px 10px; border: 1px solid #EEE; margin-bottom: 2px; display: flex; border-radius: 5px; justify-content: space-between; position: relative; align-items: center;}
.visiied li .close{display: block; width :15px; height: 15px; text-align: center; line-height: 15px; color: #FFF; border-radius: 8px; background-color: #000; font-size: 12px;}

.footer{position: relative;}
.footer .fnb{position: relative; display: flex; flex-wrap: wrap; justify-content: center; border-top: 1px solid #EEE; border-bottom: 1px solid #EEE;}
.footer .fnb li{overflow: hidden;}
.footer .fnb li a{color: #666; text-decoration: none; display: block; padding: 0 30px; line-height: 50px;}
.footer .footer_inner{position: relative; width: 94%; margin: 0 auto; padding: 50px 0; display: flex; justify-content: space-between; line-height: 1.6em;}
.footer .footer_inner .footer_left{position: relative; width: 33%;}
.footer .footer_inner .footer_left address{font-style: normal;}
.footer .footer_inner .footer_left span{font-weight: bold; color: #B21848;}
.footer .footer_inner .footer_center{position: relative; text-align: center; width: 33%;}
.footer .footer_inner .footer_center p{margin-bottom: 20px;}
.footer .footer_inner .footer_center .tel a{font-weight: bold; font-size: 2em; text-decoration: none;}
.footer .footer_inner .footer_right{position: relative; text-align: right; width: 33%; display: flex; flex-direction: column; align-items: flex-end;}
.footer .footer_inner .footer_right .bank_num{color: #B21848; font-weight: bold; font-size: 2em; margin-bottom: 20px;}
.footer .footer_inner .footer_right .sns{display: flex; margin-top: 20px;}

@media screen and (min-width:601px) {
  .best h2{font-size: 20px; line-height: 80px; text-align: center;}
  .best ul{display: flex; justify-content: space-between; width: 94%; margin: 0 auto;}
  .best ul li{width: 24%; text-align: center;}
  .best ul li img{width: 100%;}
  .best ul li h3{font-size: 13px; line-height: 2em;}
  .best ul li {font-size: 12px; white-space: nowrap; overflow: hidden;}

  .choice{position: relative; margin-bottom: 100px;}
  .choice h2{font-size: 20px; line-height: 80px; text-align: center;}
  .choice ul:nth-child(2){display: flex; width: 40%; margin: 0 auto; margin-bottom: 40px;}
  .choice ul:nth-child(2)>li{width: 25%; border: 1px solid #CCC; box-sizing: border-box; overflow: hidden;}
  .choice ul:nth-child(2)>li:first-child, .choice ul:nth-child(2)>li:nth-child(2), .choice ul:nth-child(2)>li:nth-child(3){border-right: none;}
  .choice ul:nth-child(2)>li>a{display: block; line-height: 40px; font-size: 13px; text-align: center; text-decoration: none; color: #000; background-color: #FFF;}
  .choice ul:last-child{display: flex; justify-content: space-between; width: 94%; margin: 0 auto;}
  .choice ul:last-child li{width: 25%; overflow: hidden;}
  .choice ul:last-child li img{width: 100%;}
}
@media screen and (max-width:600px) {
  .best h2{font-size: 20px; line-height: 80px; text-align: center;}
  .best ul{width: 94%; margin: 0 auto;}
  .best ul li{text-align: center; display: flex; justify-content: space-between; margin-bottom: 10px;}
  .best ul li img{width: 50%;}
  .best ul li div{width: 50%; display: flex; flex-direction: column; justify-content: center;}
  .best ul li h3{font-size: 14px; line-height: 2em;}
  .best ul li p{font-size: 12px; white-space: nowrap; overflow: hidden;}

  .choice{position: relative; margin-bottom: 50px;}
  .choice h2{font-size: 20px; line-height: 80px; text-align: center;}
  .choice ul:nth-child(2){display: flex; flex-wrap: wrap; width: 40%; margin: 0 auto; margin-bottom: 40px;}
  .choice ul:nth-child(2)>li{width: 50%; border: 1px solid #CCC; box-sizing: border-box; overflow: hidden;}
  .choice ul:nth-child(2)>li:first-child, .choice ul:nth-child(2)>li:nth-child(2){border-bottom: none;}
  .choice ul:nth-child(2)>li:first-child, .choice ul:nth-child(2)>li:nth-child(3){border-right: none;}
  .choice ul:nth-child(2)>li>a{display: block; line-height: 40px; font-size: 13px; text-align: center; text-decoration: none; color: #000; background-color: #FFF;}
  .choice ul:last-child{display: flex; flex-wrap: wrap; justify-content: space-between; width: 94%; margin: 0 auto;}
  .choice ul:last-child li{width: 49%; margin-bottom: 2%; overflow: hidden;}
  .choice ul:last-child li img{width: 100%;}
}